@font-face{
    font-family: Roboto;
    src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

*{
    box-sizing: border-box;
}

:root {
    line-height: 1.5;
    --background-color: #121212;
    --text-primary-color: #ffffff;
    --text-secondary-color: #b3b3b3;
    --selected-color: #5851db;
    --hover-color: #7270f5;
    --accent-color: #00aaff;
    --card-background: #1e1e1e;
    --border-color: #333333;
    
    --fw-thin: 100;
    --fw-regular: 500;
    --fw-bold: 600;
    --fw-black: 700;

    --font-size-small: 1.2rem;
    --font-size-main: 1.5rem;
    --font-size-large: 2.2rem;
    --font-size-header: 2.6rem;
    
    --tiny-padding: 0.7rem;
    --small-padding: 1rem;  
    --medium-padding: 1.5rem;  
    --large-padding: 2.25rem;   
    --Xl-padding: 5rem;
}


h1, h2, h3, h4, h5, figure, p, ol, ul {
    margin: 0;
    padding: 0;
}

p{
    font-size: var(--font-size-small);
    font-weight: var(--fw-thin);
    opacity: 0.6;
}


ol[role="list"], ul[role="list"] {
    list-style: none;
    padding-inline: 0;
}

li{
    list-style: none;
}

h1, h2, h3, h4, h5 {
    font-size: inherit;
    font-weight: inherit;
}

img {
    display: block;
    max-inline-size: 100%;
}

button,
input,
select,
textarea {
    font-family: inherit;
    font-size: 100%;
}

a:hover, button:hover, input[type="checkbox"]{
    cursor: pointer; 
}

/* CSS reset - end */
/*------------------------------------------------------------*/
/* Global - start */
body{
    margin: 0;
    padding: 0;
    font-family: Roboto, sans-serif;
    font-size: var(--font-size-main);
    font-weight: var(--fw-regular);
    height: 100vh;
    width: 100vw;
    background-color: var(--background-color); 
    color: var(--color-text-main);
    display: grid;
    grid-template-columns: clamp(18.75rem, 30%, 27rem) 1fr;
    grid-template-rows: repeat(2, 1fr);
}

h1{
    font-size: var(--font-size-header);
    font-weight: var(--fw-black);
    color: var(--text-primary-color);
}

h2{
    font-size: var(--font-size-large);
    font-weight: var(--fw-bold);
    color: var(--text-primary-color);
}

h3{
    font-size: var(--font-size-small);
    font-weight: var(--fw-regular);
    color: var(--text-primary-color);
}

p{
    font-size: var(--font-size-small);
    color: var(--text-secondary-color);
}

a {
    color: var(--accent-color);
  }

.modal{
    background-color:  var(--hover-color);
    color: var(--text-primary-color);
    border: none;
    border-radius: 1rem; 
    border-radius: var(--font-size-small);
    box-shadow: 0 16px 36px rgba(0, 0, 0, 0.1);
    padding: var(--large-padding);
    width: clamp(300px, 20%, 400px);
   
}

.modal > :first-child{
    margin-bottom: var(--large-padding);
    text-align: center;
}

.modal .form-row{
    display: flex;
    flex-direction: column;
    margin-bottom: var(--small-padding);
}

.modal form{
    font-size: var(--font-size-small);
    display: flex;
    flex-direction: column;
    gap: var(--small-padding);
}

.modal-btns{
    margin-top: auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    gap: var(--small-padding);
    font-size: var(--font-size-small);
}

.modal-btns > button{
    padding: 0.25em 0.25em;
    border-radius: 15px;
    border: none;
}

dialog::backdrop{
    background-color: rgba(0, 0, 0, 0.5)
    
}

/* Modal - End*/
/*------------------------------------------------------------*/
/*------------------------------------------------------------*/
/* SideBar - Start*/
.sidebar{
    grid-column: 1/2;
    grid-row:1/3; 
    padding: var(--large-padding);
    display: flex;
    flex-direction: column;
    gap: var(--Xl-padding);
    font-size: var(--font-size-small);
}


.sidebar button, .task button{
    padding:0;
    border: none;
    background: none;
    width:100%;
    text-align: left;
    color: var(--text-primary-color);
    border-radius: 0.5em;
    padding: 0.2em;
}

.highlight * {
    fill:var(--accent-color);
    color:var(--accent-color);
}

.sidebar button:focus * {
    fill:var(--accent-color);
    color:var(--accent-color);
}

.sidebar button:hover {
    
    background:#26323818;
}
.sidebar button:focus{
    background:#26323817;
    
}

.sidebar svg{
    fill: white;
    width: 1.75em;
    height:auto;
}


/* SideBar - End*/
/*------------------------------------------------------------*/
/* Content- Start*/

.page{
    grid-row:1/3;
    grid-column:2/3;
    display: grid;
    grid-template-rows: clamp( 6.25rem, 10%, 18.75rem) 1fr;
    grid-template-columns: 1fr clamp( 8rem, 30%, 20rem);
    padding-left: 5rem;
    row-gap: var(--large-padding);
}
.page-header{
    grid-row:1/2;
    grid-column:1/2;
    display:flex;
    align-items: flex-end;
    
}

.content{
    grid-row:2/3;
    grid-column:1/2;
    display:grid;
    grid-auto-rows: 2em;
    background-color: var(--background-color); 
    color: var(--text-primary-color);
}

.task{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: var(--small-padding);
}

.task img{
    filter:invert(1);
    width: 1em;
    height:auto;
}


/*------------------------------------------------------------*/
/* Utility- Start*/
.column-flex{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

.row-flex{
    display:flex;
    align-items: center;
    gap: var(--small-padding);
}

.row-flex.wide, .column-flex.wide {
    gap: var(--large-padding);
}

.row-flex.small, .column-flex.small{
    gap: var(--tiny-padding);
}

.underline {
    position: relative;
}

.underline::after {
    content: '';
    position: absolute;
    left: 0;
    bottom: 0; /* Position it at the bottom */
    width: 100%;
    height: 1px; /* Thickness of the underline */
    background-color: var(--text-secondary-color); /* Color of the underline */
    opacity: 10%;
}

input[type="checkbox"] {
    appearance: none;
    -webkit-appearance: none;
    display: flex;
    align-content: center;
    justify-content: center;
    font-size: 2rem;
    padding: 0.25rem;
    border: 0.15rem solid white;
    border-radius: var(--font-size-main);;
}
  
input[type="checkbox"]::before {
    content: "";
    width: 1rem;
    height: 1rem;
    clip-path: circle(50% at 50% 50%);
    transform: scale(0);
    background-color: var(--accent-color);
}

input[type="checkbox"]:checked{
    border-color: var(--accent-color);
}

input[type="checkbox"]:checked::before {
    transform: scale(1);
}




  
  



